  margin-bottom: 50px;
}

.event .banner-img {
  width: auto;
  max-width: 75%;
  border-radius: 8px;
}

@media only screen and (max-width: 450px) {
  .event .banner-img {
    display: none;
  }
}

.event .section-subtitle {
  margin-bottom: 15px;
}

.event .section-title {
  margin-bottom: 30px;
}

.event-card-group {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  gap: 40px;
}

.event-card {
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  align-items: center;
  text-align: left;
  background: #fff;
  border-radius: 8px;
  padding: 10px 25px;
  margin-right: 25px;
  border-right: 7.5px solid transparent;
  box-shadow: 0 10px 30px hsla(0, 0%, 0%, 0.1);
  transition: 0.25s ease;
}

.event-card .content-left {
  width: 15%;
  text-align: left;
  padding: 5px;
  box-shadow: 0 16px 0 -15px #ffffff80;
}

.event-card .stock {
  font-family: "Josefin Sans", sans-serif;
  font-size: 25px;
  font-weight: 700;
  color: hsl(216, 98%, 52%);
}

.event-card .event-name {
  color: hsl(217, 100%, 20%);
  font-size: 20px;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
  transition: 0.25s ease;
}

.event-card:hover {
  border-color: #ef2f88;
}

.event-card:hover .event-name {
  color: #ef2f88;
}

@media only screen and (max-width: 450px) {
  .event {
    padding: 30px;
    /* background: #0780ff50; */
  }

  .event-card {
    display: flex;
    align-items: center;
    text-align: left;
  }

  .event-card .content-left {
    width: 100px;
    padding-bottom: 0;
    margin-bottom: 0;
    padding-right: 15px;
    margin-right: 25px;
    box-shadow: 1px 0 0 0 #ffffff80;
  }

  .event-card .event-name {
    text-align: left;
  }
}

@media (min-width: 992px) {
  .event {
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 30px;
